import * as cookie from './cookie';
import * as meta from './meta';
import * as product from './product';
import * as shop from './shop';
import * as trekkie from './trekkie';

import * as typedef from './typedef';

const flatten = require('lodash.flatten');

declare type SummaryEntry = typedef.SummaryEntry;

export function getRawProperties():SummaryEntry[][] {

    const props: SummaryEntry[][] = [
        cookie.getCookieProperties(),
        meta.getMetaProperties(),
        product.getPageProperties(),
        shop.getShopProperties(),
        trekkie.getTrekkieProperties()
    ];

    return props;
}

export function getProperties(): SummaryEntry[] {

    return flatten(getRawProperties());
}