const get = require('lodash.get');

import * as typedef from './typedef';

declare type PropertyMapping = typedef.PropertyMapping;

export function getPropertyMapping(): PropertyMapping {

    const propertyMapping: PropertyMapping = [
        { alias: 'page_type', property: 'page.pageType' },
        { alias: 'product_id', property: 'product.id' },
        { alias: 'product_gid', property: 'product.gid' },
        { alias: 'product_type', property: 'product.vendor' },
        { alias: 'product_vendor', property: 'product.type' },
        { alias: 'resource_id', property: 'page.resourceId' },
        { alias: 'resource_type', property: 'page.resourceType' }
    ];

    return propertyMapping;
}

export function getPropertyValue(key: string) {

    const root: any = globalThis.ShopifyAnalytics?.meta;

    return root ? get(root, key) : null;
}

export function getMetaProperties(options?: { mapping: PropertyMapping }) {

    const propertyMapping = options?.mapping || getPropertyMapping();

    return propertyMapping.map(function ( { alias, property }, _) {
        return { [alias]: getPropertyValue(property) };
    });
}