import * as typedef from './typedef';

declare type PropertyMapping = typedef.PropertyMapping;

export function getPropertyMapping(): PropertyMapping {

    const propertyMapping: PropertyMapping = [
        { alias: 'page_url', property: 'og:url' },
        { alias: 'page_type', property: 'og:type' },
        { alias: 'page_title', property: 'og:title' },
        { alias: 'product_price', property: 'og:price:amount' }
    ];

    return propertyMapping;
}

export function getPropertyValue(key: string, value: string) {

    const selector: string = `meta[property="${key}"]`;

    return document.querySelector(selector)?.getAttribute(value);
}

export function getPageProperties(options?: { mapping: PropertyMapping }) {

    const propertyMapping = options?.mapping || getPropertyMapping();

    return propertyMapping.map(function ( { alias, property }, _) {
        return { [alias]: getPropertyValue(property, `content`) };
    });
}