import axios, { AxiosResponse } from 'axios';


export function getProductHandle(options?: { baseUrl: string }) {

    const baseUrl = options?.baseUrl || window.location.pathname;

    return baseUrl.split('/').pop();
}

export async function fetchProductPage(options?: { handle: string }) {

    const handle = options?.handle || getProductHandle();

    let response: AxiosResponse<any> | null = null;

    try {
        response = await axios.get(`/products/${handle}.json`);
    } catch (error) {
        response = error.response;
    } finally {
        return response?.status === 200
    }
}