import * as PropsBuilder from './props';
import * as SummaryBuilder from './summary';
import * as typedef from './typedef';

declare type Summary = typedef.Summary;
declare type SummaryEntry = typedef.SummaryEntry;

export async function getSummary(): Promise<Summary> {

    const props: SummaryEntry[] = PropsBuilder.getProperties();
    
    const summary: Summary = SummaryBuilder.getSummary(props);

    console.log(summary);

    const patched = SummaryBuilder.patchSummary(summary, {
        include: [
            'page_type', 'product_id', 'product_title',
            'shop', 'shop_id', 'unique_token', 'visit_token'
        ]
    });

    console.log(patched);
    
    return await SummaryBuilder.validateSummary(patched);
}