import * as api from './api';
import * as typedef from './typedef';

declare type Summary = typedef.Summary;
declare type SummaryEntry = typedef.SummaryEntry;


export function getSummary(props: SummaryEntry[]): Summary {

    const summary: Summary = {} as Summary;

    props && props.forEach(function (entry: SummaryEntry) {

        for (let [key, value] of Object.entries(entry)) {

            if (summary[key] && value === 'window') {
                continue;
            }

            if (value !== undefined && value !== null) {
                summary[key] = value;
            }
        }
    });

    return summary;
}

export function patchSummary(summary: Summary, options?: { include: string[] }): Summary {

    const include: string[] = options?.include || Object.keys(summary);

    const result: Summary = {} as Summary;

    include.forEach(key => result[key] = summary[key]);

    return result;
}

export async function validateSummary(summary: Summary): Promise<Summary> {

    if (summary && summary['pageType']) return summary;

    const isProductPage = await api.fetchProductPage();
    if (isProductPage) summary['pageType'] = "product";

    return summary;
}